import React from "react"
import { connect } from "react-redux"
import { RouteChildrenProps } from "react-router"
import ContentBox from "../../components/ContentBox"
import ViewHeader from "../../components/ViewHeader"
import { AppState } from "../../redux/root"
import { DataTypes } from "../../service/data/demo_data"
import Card from "./card/Card"
import { ICard } from "./card/redux/types"
import { SCards } from "./styles"

interface IOwnProps { }

interface IStateProps {
    cards: Array<ICard<DataTypes>>
}

const MapStateToProps = (state: AppState): IStateProps => {

    return {
        cards: state.cards,
    }
}

type Props = IOwnProps & RouteChildrenProps & IStateProps



const AllCardsView: React.FC<Props> = (props) => {
    const { cards, history } = props
    return (
        <SCards>
            <ViewHeader
                title={ "All Cards" }
                btn="close"
                btnClickHandler={ () => history.push("/") }
            />

            <ContentBox>
                {
                    (cards.map((card, i) =>
                        <Card { ...card } key={ i } />,
                    ))
                }
            </ContentBox>
        </SCards>
    )
}

export default connect(MapStateToProps)(AllCardsView)
